<template>
  <div :class="$style.comment" class="van-hairline--top">
    <div :class="$style.title">
      <h3 id="reply">评论·{{count}}</h3>
    </div>
    <div v-if="comments.length" :class="$style.comments">
      <v-comment v-for="(item, index) in comments" :key="index" :comment="item"></v-comment>
    </div>
    <div :class="$style.info" v-if="count === 0">
      快来抢沙发!!!!
    </div>
    <div v-else :class="$style.footer">
      到底了....
    </div>
  </div>
</template>

<script>
// import { mapState } from 'vuex'
import Comment from '@/components/Comment'
export default {
  name: 'x-comment',
  props: {
    comments: {
      type: Array,
      required: true
    },
    count: {
      type: Number,
      required: true
    }
  },
  components: {
    [Comment.name]: Comment
  }
}
</script>

<style module>
.comment{
  width: 100%;
  min-height: 100px;
  padding-bottom: 50px;
}
.info{
  width: 100%;
  height: 100px;
  background-color: #eeeeee;
  color: #999999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title{
  widows: 100%;
  height: 40px;
  display: flex;
  font-size: 20px;
  font-weight: bold;
  box-sizing: border-box;
  border-left: 4px solid #3388ff;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-indent: 1em;
}
.footer{
  width: 100%;
  height: 80px;
  background-color: #eeeeee;
  color: #999999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.comments{
  padding: 0 10px;
}
</style>



