<template>
  <footer :class="$style.reply" class="van-hairline--top">
    <input type="text" :placeholder="placeholder" :class="$style.input" v-model="text" @keydown.enter="uploadReply"/>
    <van-button type="primary" :class="$style.button" size="small" @click="uploadReply">发布</van-button>
  </footer>
</template>

<script>
import { mapGetters } from 'vuex'
import { postReply } from '../../util/api'
import { Button, Toast } from 'vant'
const placeholders = {
  low: '抢占前排!',
  middle: '来蹭热闹?',
  high: '我来说两句'
}
export default {
  name: 'x-reply',
  components: {
    [Button.name]: Button
  },
  props: {
    id: {
      type: String,
      required: true
    },
    count: {
      type: Number,
      required: true
    },
    value: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      text: '',
      showNames: false
    }
  },
  computed: {
    placeholder () {
      if (this.count <= 5) {
        return placeholders.low
      } else if (this.count <= 30) {
        return placeholders.middle
      } else {
        return placeholders.high
      }
    },
    names () {
      return [...new Set(this.value.map((v) => v.author.loginname))]
    },
    ...mapGetters(['token', 'user', 'addLogo'])
  },
  methods: {
    async uploadReply () {
      let content = this.text
      if (this.addLogo) {
        content += ` \n 来自PWA[CNode.js社区](https://alexlees.github.io/CNode/dist/index.html)`
      }
      if (!this.token) {
        Toast.fail('需要登陆')
        this.$router.push('/login')
      } else {
        try {
          const res = await postReply({topic_id: this.id, content, accesstoken: this.token})
          if (res.success) {
            Toast.success('评论成功\n下拉刷新看看')
            const newReply = {}
            newReply.reply_id = res.reply_id
            newReply.id = this.id
            newReply.is_uped = false
            newReply.content = this.text
            newReply.create_at = new Date()
            newReply.author = this.user
            newReply.ups = []
            this.$emit('input', [...this.value, newReply])
            this.text = ''
          }
        } catch (error) {
          Toast.fail('视乎网络有问题?')
          console.log(error)
        }
      }
    }
  },
  deactivated () {
    this.text = ''
  }
}
</script>

<style module>
.reply{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  padding: 0 10px;
}
.input{
  flex: 1;
  height: 30px;
  line-height: 30px;
  box-sizing: border-box;
  appearance: none;
  background-color: #eeeeee;
  border: none;
  text-indent: 1em;
}
.button{
  height: 30px;
  border: none;
  border-radius: 0;
  font-size: inherit;
}
</style>



