<template>
  <router-link :class="$style.author" tag="div" :to="to">
    <div :class="$style.avatar" :style="styleObj"></div>
    <div :class="$style.loginname">{{author.loginname}}</div>
  </router-link>
</template>

<script>
const keys = ['loginname', 'avatar_url']
export default {
  name: 'v-author',
  props: {
    author: {
      required: true,
      validator (v) {
        if (typeof v !== 'object') {
          return false
        }
        return keys.every((key) => typeof v[key] !== 'undefined')
      }
    }
  },
  computed: {
    styleObj () {
      return {
        backgroundImage: `url(${this.author.avatar_url})`
      }
    },
    to () {
      return `/user/${this.author.loginname}`
    }
  }
}
</script>

<style module>
.author{
  width: 100%;
  height: 46px;
  line-height: 46px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.avatar{
  width: 40px;
  height: 40px;

  background-size: cover;
  background-position: center center;
  background-size: cover;
  vertical-align: middle;
  border-radius: 50%;
}
.loginname{
  text-indent: 1em;
}
</style>



