<template>
  <div :class="$style.topic" v-if="data">
    <x-header :author="data.author" v-model="data.is_collect" :id="data.id"/>
    <van-pull-refresh v-model="isLoading" @refresh="getTopic">
      <div :class="$style.reload">
        <x-title>
          {{title}}
        </x-title>
        <v-markdown :content="data.content"/>
        <x-comment :comments="data.replies" :count="data.reply_count"></x-comment>
        <div v-if="!data" :class="$style.info">下拉刷新试试!</div>
      </div>
    </van-pull-refresh>
    <x-reply :count="data.reply_count" :id="data.id" v-model="data.replies"></x-reply>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { PullRefresh } from 'vant'
import { fetchTopic } from '@/util/api'
import Header from './Header'
import Reply from './Reply'
import Title from './Title'
import Comment from './Comment'
import MarkDown from '@/components/MarkDown'
export default {
  name: 'page-topic',
  components: {
    [Header.name]: Header,
    [MarkDown.name]: MarkDown,
    [Comment.name]: Comment,
    [Reply.name]: Reply,
    [Title.name]: Title,
    [PullRefresh.name]: PullRefresh
  },
  data () {
    return {
      data: null,
      isLoading: false
    }
  },
  methods: {
    async getTopic () {
      this.$toast.loading({
        mask: true,
        message: '加载中...'
      })
      try {
        const res = await fetchTopic({id: this.$route.params.id, accesstoken: this.token})
        if (res.success) {
          this.data = res.data
        }
        this.$toast.clear()
        setTimeout(() => {
          this.isLoading = false
        }, 500)
      } catch (error) {
        this.$toast.clear()
        this.$toast.fail('网络有问题?\n下拉试试!')
        this.isLoading = false
        console.error(error)
      }
    }
  },
  activated () {
    this.getTopic()
  },
  computed: {
    ...mapGetters(['token']),
    title () {
      if (this.data) {
        return this.data.title
      } else {
        return ''
      }
    }
  }
}
</script>

<style module>
.topic{
  width: 100%;
  background-color: #ffffff;
}
.reload{
  width: 100%;
  min-height: calc(100vh - 46px);
}
.info{
  text-align: center;
  line-height: 120%;
  font-weight: bold;
  font-size: 14px;
}
</style>



