<template>
  <div class="markdown-body" v-html="content">
  </div>
</template>

<script>
import 'github-markdown-css'
export default {
  name: 'v-markdown',
  props: {
    content: {
      type: String
    }
  }
}
</script>

<style>
.markdown-body {
  box-sizing: border-box;
  min-width: 200px;
  max-width: 980px;
  margin: 0 auto;
  padding: 45px;
  font-size: 14px;
}

@media (max-width: 767px) {
  .markdown-body {
    padding: 15px;
  }
}
</style>



