<template>
  <div :class="$style.comment" class="van-hairline--bottom">
    <v-author :author="comment.author" @click-right="change">
      <div>
        <i class="iconfont" :class="icon"></i>
        <span>{{ups | noZero}}</span>
      </div>
    </v-author>
    <v-markdown :content="comment.content" :class="$style.content"></v-markdown>
    <div :class="$style.footer"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { Toast } from 'vant'
import Author from './Author'
import MarkDown from './MarkDown'
import { postUpReply } from '@/util/api'

export default {
  name: 'v-comment',
  props: {
    comment: {
      type: Object
    }
  },
  data () {
    return {
      uped: false,
      ups: 0
    }
  },
  components: {
    [Author.name]: Author,
    [MarkDown.name]: MarkDown
  },
  computed: {
    ...mapGetters(['token']),
    icon () {
      return this.uped ? 'icon-appreciatefill' : 'icon-appreciate'
    }
  },
  methods: {
    change () {
      if (!this.token) {
        this.$toast.fail({
          duration: 500,
          message: '需要登陆!'
        })
        return
      }
      this.uped = !this.uped
      if (this.uped) {
        this.ups += 1
      } else {
        this.ups -= 1
      }
      this.postUp()
    },
    initData () {
      setTimeout(() => {
        this.uped = this.comment.is_uped
        this.ups = this.comment.ups.length
      }, 20)
    },
    async postUp () {
      if (this.token) {
        try {
          await postUpReply({accesstoken: this.token, reply_id: this.comment.id})
        } catch (error) {
          console.error(error)
        }
      } else {
        Toast.fail('需要登陆!')
      }
    }
  },
  filters: {
    noZero (v) {
      if (v === 0) {
        return ''
      }
      return v
    }
  },
  created () {
    this.initData()
  }
}
</script>

<style module>
.comment{
  width: 100%;
  min-height: 100px;
}
.content{
  margin: 0 10px;
}
</style>



