<template>
  <v-header @click-right="change">
    <v-author :author="author" :class="$style.author"/>
    <i class="iconfont" :class="icon" slot="right"></i>
  </v-header>
</template>

<script>
import { mapGetters } from 'vuex'
import Header from '@/base/Header'
import Author from './Author'
import { postTopicCollect, postTopicDeCollect } from '../../util/api'
export default {
  name: 'x-header',
  components: {
    [Header.name]: Header,
    [Author.name]: Author
  },
  computed: {
    ...mapGetters(['token']),
    icon () {
      return this.value ? 'icon-favorfill' : 'icon-favor'
    }
  },
  props: {
    author: {
      type: Object,
      required: true
    },
    value: {
      type: Boolean,
      default: false
    },
    id: {
      type: String,
      required: true
    }
  },
  methods: {
    change () {
      this.postCollect(this.value)
    },
    async postCollect (v) {
      if (!this.token) {
        this.$toast.fail({
          mask: true,
          message: '需要登陆!',
          duration: 500
        })
        return
      }
      if (v) {
        try {
          await postTopicDeCollect({accesstoken: this.token, topic_id: this.id})
          this.$emit('input', false)
          this.$toast.success({
            message: '取消收藏!',
            duration: 1000
          })
        } catch (error) {
          console.log(error)
        }
      } else {
        try {
          await postTopicCollect({accesstoken: this.token, topic_id: this.id})
          this.$emit('input', true)
          this.$toast.success({
            message: '收藏成功!',
            duration: 1000
          })
        } catch (error) {
          console.log(error)
        }
      }
    }
  }
}
</script>

<style module>
.author{
  text-align: center;
}
</style>



